@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
.navbar {
  background: linear-gradient(90deg, rgb(26, 25, 25) 0%, rgb(43, 38, 38) 100%);
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70px;
  max-width: 1440px;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.fa-sync {
  margin-left: 0.8rem;
  font-size: 1.7rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover {
  border-bottom: 4px solid #fff;
  transition: all 0.2s ease-out;
  text-decoration: none;
  color: #fff;
}

.fa-bars {
  color: #fff;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 70px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.4s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }
}

.footer-container {
  background-color: #252323;
  padding: 1rem 0 1rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.social-icon-link {
  color: #fff;
  font-size: 24px;
}

.social-media {
  width: 100%;
}

.social-media-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1000px;
  margin: 10px auto 0 auto;
}

.social-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 130px;
}

.copyright,
.footer-logo {
  color: #fff;
  margin-bottom: 16px;
}

@media screen and (max-width: 820px) {
  .footer-links {
    padding-top: 2rem;
  }

  .social-media-wrapper {
    flex-direction: column;
  }
}

@media screen and (max-width: 768px) {
  .footer-links {
    padding-top: 2rem;
  }

  .social-media-wrapper {
    flex-direction: column;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

.cards {
  padding: 4rem;
  background: #fff;
}

h1 {
  text-align: center;
}

.cards-container {
  display: flex;
  flex-flow: column;
  align-items: center;
  margin: 0 auto;
  max-width: 1200px;
  width: 90%;
}

.cards-wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.cards-items {
  margin-bottom: 25px;
}

.cards-item {
  display: flex;
  flex: 1 1;
  margin: 0 1rem;
  border-radius: 10px;
}

.cards-item-link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 5px 15px rgba(24, 99, 238, 0.2);
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards-item-link:hover {
  text-decoration: none;
}

.cards-item-pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 55%;
  overflow: hidden;
}

/* Applies labels to images */
.cards-item-pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 14px;
  font-weight: 550;
  color: #fff;
  background-color: #14bd98;
  box-sizing: border-box;
}

.cards-item-img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards-item-img:hover {
  transform: scale(1.15);
}

.cards-item-info {
  padding: 20px 30px 30px;
}

.cards-item-text {
  color: #292424;
  font-size: 16px;
  line-height: 20px;
}

@media only screen and (min-width: 1200px) {
  .content-media-container {
    width: 85%;
  }
}

@media only screen and (min-width: 1024px) {
  .cards-items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .cards-item {
    margin-bottom: 2rem;
  }
}

.hero-container {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url(/home/static/media/home.e122db8b.jpg) center center/cover no-repeat;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  object-fit: contain;
  text-align: center;
}

.hero-container > h1 {
  color: #fff;
  font-size: 90px;
  margin-top: -85px;
}

.hero-container > p {
  margin-top: 8px;
  color: #fff;
  font-size: 30px;
  font-family: "Roboto", "Trebuchet MS", Arial, sans-serif;
}

@media screen and (max-width: 960px) {
  .hero-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 768px) {
  .hero-container > h1 {
    font-size: 50px;
    margin-top: -85px;
  }

  .hero-container > p {
    font-size: 20px;
  }
}

.about-container {
  background-color: rgba(256, 256, 256, 0.5);
  width: 60vw;
  height: 40vh;
  padding: 1rem;
}

h1 {
  color: #000000;
  font-family: "Roboto", serif;
  line-height: 1.4;
  font-size: 1.8rem;
  margin-bottom: 1rem;
}

.about-container p {
  color: #303036;
  text-align: left;
  font-size: 1.1rem;
}

p > a {
  color: #001e35;
}

@media screen and (max-width: 1505px) {
  .about-container {
    overflow: scroll;
    overflow-x: hidden;
  }
}

.form-container {
  background-color: rgba(256, 256, 256, 0.5);
  width: 40vw;
  height: 50vh;
  padding: 1rem;
}

.form-row {
  margin-bottom: 2em;
}

/* Styles button to fit with theme */
.submit-btn {
  font-size: 18px;
  font-weight: 500;
  border: 3px solid #fff;
  background-color: transparent;
  color: #fff;
  padding: 8px 20px;
  transition: all 0.3s ease-out;
}

.submit-btn:hover {
  transition: all 0.3s ease-out;
  background: #fff;
  color: #242424;
  transition: 250ms;
}

